@import "header";
// @import "variables";

h1 {
  font-family: $fontHigh;
  font-size: 1.5em;
}

h3, h4 {
  font-family: $fontLow;
  font-size: 2em;
}

body {
  background:#fffff;
    background: url(../img/profil/profil.jpg) no-repeat center center fixed;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    background-attachment: fixed;
}

fieldset {
  background-color: rgba(255, 255, 255, .15);
  backdrop-filter: blur(5px);
  padding: 15px;
}

.container{
  margin-top: 10%;
  margin-bottom: 10%;
}

 .user-image{
   img {
     width: 20%;
     // height: 50%;
   }
 }

 .style1 {
   background-color: $red;
 }

li {
  list-style-type: none;
}

